#projects{
    min-height: calc(100vh-4rem);
    width: 100vw;
    max-width: 60rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    text-align: center;
    padding: 0 1rem;
}
.worksTitle{
    margin: 1rem 0;
    font-size: 3rem;


}
.project-card{
    display: flex;
    gap: 40px;
    /* margin: 50px; */
    margin-bottom: 20px;
}
.right>a{
    margin: 10px;
}
.project-tech-stack{
    border: 2px solid red;

    /* background-color: yellow; */
    /* text-align: center; */
    /* align-items: center; */
    padding-top: 5px;
    margin-left: 30px;

    border-radius: 20%;
    height: 40px;
    width: 90px;
}
.worksDesc{
    font-weight: 300;
    font-size: 1rem;
    max-width: 45rem;

}
.worksImages{
 display: flex;
 align-items: center;
 justify-content: center;
 flex-wrap: wrap;
 width: 100vw;
 max-width: 65rem;
 
}
.worksImg{
    object-fit: cover;
    height: 20rem;
    margin: 0.5rem;
}
.worksBtn{
    padding: 0.5rem 2.5rem;
    border: none;
    background: white;
    border-radius: 2rem;
    font-size: 1rem;
    margin: 3rem 0;
    font-weight: 500;
}
@media screen and (max-width:585px) {
    .worksImg{
        height: 48vw;
    }
    
}